body {
  background: -webkit-linear-gradient(300deg,#53f 15%,#05d5ff 70%,#a6ffcb 94%);
  background: linear-gradient(150deg,#53f 15%,#05d5ff 70%,#a6ffcb 94%);
  background-image: -webkit-linear-gradient(300deg, rgb(85, 51, 255) 15%, rgb(5, 213, 255) 70%, rgb(166, 255, 203) 94%);
  background-image: linear-gradient(150deg, rgb(85, 51, 255) 15%, rgb(5, 213, 255) 70%, rgb(166, 255, 203) 94%);
  background-size: 100vw 100vh;
  background-repeat: no-repeat;
  color: white;
  font-family: Open Sans, Segoe UI, sans-serif;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}


.brandDiv {
  display: flex;
  flex: 1 1;
}
.navbar-brand {
  display: inline-block;
  padding-top: .3125rem;
  padding-bottom: .3125rem;
  margin-right: 1rem;
  font-size: 1.25rem;
  line-height: inherit;
  white-space: nowrap;
}
a {
  text-decoration: none;
  background-color: transparent;
}
img {
  vertical-align: middle;
  border-style: none;
}
.contactDiv {
  display: flex;
  flex-direction: row;
  flex: 1 1;
  justify-content: flex-end;
}

@media (max-width: 800px){
  .contactDiv {
      display: none;
  }
}
.contactUL {
  display: flex;
  flex-direction: column;
  list-style: none;
}
.contactLink {
  color: white;
  padding: 8px;
}

.modal {
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}
.modalContent {
  -webkit-transition: opacity 2s linear;
  transition: opacity 2s linear;
  height: auto;
  min-height: 85%;
  border-radius: 0;
  background-image: url(/static/media/navModalBackground.1713d687.jpg);
  background-size: 100% 100%;
}
.closeDiv {
  display: flex;
  justify-content: flex-end;
  padding-bottom: 40px;
}
.closeModalButton {
  margin: 20px 30px 0 0;
  border: none;
  color: rgba(0,0,0,0.5);
}
.closeModalButton:hover {
  color: rgba(0,0,0,0.8);
  cursor: pointer;
}
.modalBody {
  display: flex;
  justify-content: center;
  color: black;
}
.modalUl {
  padding: 0;
}
.modalLi {
  display: flex;
  justify-content: center;
}
.modalLinks {
  font-family: Open Sans, Segoe UI, sans-serif; 
  letter-spacing: .1em;
  font-size: 16px;
  color: rgba(0,0,0,0.65);
  padding: 5px;
}
.modalLinks:hover {
  color: rgba(0,0,0,0.9);
  cursor: pointer;
}

.hamburgerDiv {
  display: block;
  padding-right: 20px;
}
#hamburgerBtn {
  background-color: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.77);
}
@media (min-width: 800px) {
  .hamburgerDiv {
    display: none;
  }
}

.navbarToggler {
  position: aboslute;
  cursor: pointer;
  right: 20px;
  top: 10px;
  padding: .25rem .75rem;
  font-size: 1.25rem;
  line-height: 1;
  background-color: transparent;
  border: 1px solid transparent;
  border-radius: .25rem;
}

.modalHidden {
  -webkit-transition: opacity 3s;
  transition: opacity 3s;
  opacity: 0;
}
.modalInitial {
  display: flex;
  justify-content: center;
  height: 100%;
  width: 100%;
  opacity: 0;
  -webkit-transition: opacity 3s;
  transition: opacity 3s;
  position: fixed; /* Stay in place */
  z-index: -5000; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
